import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from "@angular/common/http";
import { Observable } from 'rxjs';
import { Project } from '../model-classes/project';
import { map } from "rxjs/operators";
import { environment as env } from '../../environments/environment';
//import { environment as env } from "envi";

@Injectable({
  providedIn: 'root'
})
export class ProjectsService {

  constructor(private http: HttpClient) { }

  insert(data): Observable<boolean> {
    return this.http.post<any>(`${env.apiUrl}/projects`, data);
  }

  addSingle(project_id, user_id, role_id){
    return this.http.post<any>(`${env.apiUrl}/projects/addsingle`, {project_id, user_id, role_id});
  }

  getAll(): Observable<Project[]> {
    return this.http.get<any>(`${env.apiUrl}/projects`)
      .pipe(map(result => {
        console.log(result.msg);
        return result;
      }));
  }

  get(id): Observable<Project[]> {
    return this.http.get<any>(`${env.apiUrl}/projects/` + id);
  }

  delete(id){
    return this.http.delete<any>(`${env.apiUrl}/projects/` + id);
  }

  getRoles(){
    return this.http.get<any>(`${env.apiUrl}/roles`);
  }

  getUsersFromProject(id){
    return this.http.get<any>(`${env.apiUrl}/projects/list/` + id);
  }

  deleteUserFromProject(role,idUser,idProject){
    return this.http.post<any>(`${env.apiUrl}/projects/detachuser` ,{user_id:idUser,role_id:role,project_id:idProject});
  }

  insertComplete(data){
      return this.http.post<any>(`${env.apiUrl}/projects/createmass`, data);
  }

  saveSection(id, section, content){
      return this.http.post<any>(`${env.apiUrl}/projects/` + id + '/documents', {section:section, content:content, project_id: id});
  }

  getSection(id, section){
    return this.http.get<any>(`${env.apiUrl}/projects/` + id + '/documents?section=' + section + "&project_id=" + id);
  }

  docRequisitos(id){
    return this.http.post<any>(`${env.apiUrl}/projects/` + id + '/documents/requisitos', {project_id: id});
  }

  docDiseno(id, completo){
      console.log(completo);
      if(completo)
        return this.http.post<any>(`${env.apiUrl}/projects/` + id + '/documents/completodiseno', {project_id: id});
      else
        return this.http.post<any>(`${env.apiUrl}/projects/` + id + '/documents/diseno', {project_id: id});
  }

  docModulos(id){
    return this.http.post<any>(`${env.apiUrl}/projects/` + id + '/documents/modulos', {project_id: id});
  }

  docCasoPrueba(id){
    return this.http.post<any>(`${env.apiUrl}/projects/` + id + '/documents/casoprueba', {project_id: id});
  }

  docCompleto(id){
    return this.http.post<any>(`${env.apiUrl}/projects/` + id + '/documents/historico', {project_id: id});
  }


}
