    formatUserReqColumns(data, type) {
        let str = '';
        switch (type) {
            case 'priority':
                if (data == 0) {
                    str = 'Deseable';
                } else if (data == 1) {
                    str = 'No deseable';
                } else {
                    str = 'Critica';
                }
                break;
            case 'stability':
                if (data == 0) {
                    str = 'Transable';
                } else {
                    str = 'Intransable';
                }
                break;
            case 'status':
                if (data == 0) {
                    str = 'Pendiente';
                }else if(data==1){
                    str = 'Finalizado';
                }else if(data==2){
                    str = 'Ambiguo'
                }
                break;
        }
        return str;
    }

