  downloadFile(){
    this.proyectoService.docCompleto(this.id).subscribe((resp) => {
        const byteArray = new Uint8Array(atob(resp.pdf).split('').map(char => char.charCodeAt(0)));
        let blob = new Blob([byteArray], {type: 'application/pdf'});
        const url = window.URL.createObjectURL(blob);
        // i.e. display the PDF content via iframe
        //location.href = url;
        window.open(url, "_blank");
    });
  }

  downloadFile2(rol){
      switch(rol){
          case 'analista':
            this.proyectoService.docRequisitos(this.id).subscribe((resp) => {
                const byteArray = new Uint8Array(atob(resp.pdf).split('').map(char => char.charCodeAt(0)));
                let blob = new Blob([byteArray], {type: 'application/pdf'});
                const url = window.URL.createObjectURL(blob);
                // i.e. display the PDF content via iframe
                //location.href = url;
                window.open(url, "_blank");
            });
            break;
        case 'diseador':
            this.proyectoService.docDiseno(this.id, (this.proyecto.doctype == 1) ? false : true).subscribe((resp) => {
                const byteArray = new Uint8Array(atob(resp.pdf).split('').map(char => char.charCodeAt(0)));
                let blob = new Blob([byteArray], {type: 'application/pdf'});
                const url = window.URL.createObjectURL(blob);
                // i.e. display the PDF content via iframe
                //location.href = url;
                window.open(url, "_blank");
            });
            break;
        case 'testing':
            this.proyectoService.docCasoPrueba(this.id).subscribe((resp) => {
                const byteArray = new Uint8Array(atob(resp.pdf).split('').map(char => char.charCodeAt(0)));
                let blob = new Blob([byteArray], {type: 'application/pdf'});
                const url = window.URL.createObjectURL(blob);
                // i.e. display the PDF content via iframe
                //location.href = url;
                window.open(url, "_blank");
            });
            break;
        case 'implementador':
            this.proyectoService.docModulos(this.id).subscribe((resp) => {
                const byteArray = new Uint8Array(atob(resp.pdf).split('').map(char => char.charCodeAt(0)));
                let blob = new Blob([byteArray], {type: 'application/pdf'});
                const url = window.URL.createObjectURL(blob);
                // i.e. display the PDF content via iframe
                //location.href = url;
                window.open(url, "_blank");
            });
            break;
      }

  }



    openAddDialog() {
        const dialogRef = this.dialog.open(EditProjectComponent, {
            width: '850px',
            data: 'This text is passed into the dialog',
            disableClose: true,
            autoFocus: true
        });
        dialogRef.afterClosed().subscribe(result => {
            console.log(`Dialog closed: ${result}`);
            this.dialogResult = result;
            // tslint:disable-next-line: triple-equals
            if (result == 'Confirm') {
                this.toastr.success('Proyecto agregado exitosamente', 'Notificacin', { timeOut: 3000 });
            }
        });
    }

