    deleteUserReq(id: string) {
        this.openDeletionConfirmationDialog('requisito de usuario').afterClosed().subscribe(confirmation => {
            console.log(confirmation);
            if (confirmation.confirmed) {
                this.userReqService.delete(id).subscribe({
                    next: result => {
                        console.log(result);
                        this.loadData();
                        this.toastr.success('Requisito de usuario eliminado correctamente', 'Notificacin', { timeOut: 3000 });
                    },
                    error: result => {
                        console.log(result);
                    }
                });
            }
        });
    }

    deleteSoftReq(id: string) {
        this.openDeletionConfirmationDialog('requisito de software').afterClosed().subscribe(confirmation => {
            console.log(confirmation);
            if (confirmation.confirmed) {
                this.softReqService.delete(id).subscribe({
                    next: result => {
                        console.log(result);
                        this.loadData();
                        this.toastr.success('Requisito de software eliminado correctamente', 'Notificacin', { timeOut: 3000 });
                    },
                    error: result => {
                        console.log(result);
                    }
                });
            }
        });
    }

    openDeletionConfirmationDialog(title) {
        var deletionDialogConfig = this.getDialogConfig();
        deletionDialogConfig.data = { message: 'Desea eliminar este ' + title + '?' };
        return this.dialog.open(ConfirmationDialogComponent, deletionDialogConfig);
    }

    getDialogConfig() {
        const dialogConfig = new MatDialogConfig();

        dialogConfig.disableClose = true;
        dialogConfig.autoFocus = true;
        return dialogConfig;
    }

  public onReady( editor ) {
    editor.ui.getEditableElement().parentElement.insertBefore(
        editor.ui.view.toolbar.element,
        editor.ui.getEditableElement()
    );
}

deleteMember(member_email,role)
{
    console.log("sadas");
    var idEstudiante;
    for (let estudiante of this.equipo) {
        if(estudiante.email==member_email)
        {
            idEstudiante = estudiante.id;
        }
    }

    var idRol;
    for (let rolb of this.roles) {
        if(rolb.name==role)
        {
            idRol = rolb.id;
        }
    }
    this.proyectoService.deleteUserFromProject(idRol,idEstudiante,this.id).subscribe
    (
        (data) =>
        {
           this.loadData();
        });

}

formatDate(date) {
    return moment(date).format('DD-MM-YYYY');
}

formatUrgency(value) {
    console.log("VALUE", value);
    if (value == 1) {
        return 'Urgente'
    }
    else if (value == 2) {
        return 'Normal'
    } else {
        return 'Si se puede'
    }
    }


